import { SnackbarProps } from '@mui/material/Snackbar';
import { type IAction } from '@strife/common';
import { ServiceIdentifier } from "inversify";

export interface IToastService {
  active?: IToastData;
  key?: string;
  onChange: IAction<[]>;
  add(toastData: IToastData): IToastHandle;
  remove(toastHandle: IToastHandle): void;
}

export type IToastData = SnackbarProps;

export interface IToastHandle {
  dismiss(): void;
}

export const ToastServiceServiceId: ServiceIdentifier<IToastService> = Symbol.for('ToastServiceServiceId');