'use client'

import Snackbar from '@mui/material/Snackbar';
import { Fragment, JSX, useEffect, useState } from 'react';
import di from '../src/di';
import '../src/services/toast.service';
import { type IToastData, ToastServiceServiceId } from '../src/services/toast.service.interface';

export default function Toasts(): JSX.Element {
  const toastService = di.get(ToastServiceServiceId);
  const [ snackbar, setSnackbar ] = useState<JSX.Element | undefined>();
  useEffect(initializationEffect, []);

  return <Fragment>{snackbar}</Fragment>;

  function onChange(): void {
    const snackbar = toastService.active
      ? <Snackbar key={toastService.key} {...toastService.active} />
      : <Fragment />;
    setSnackbar(snackbar);
  }

  function initializationEffect(): void {
    onChange();
    toastService.onChange.add(onChange);
  }
}
