import { SnackbarProps, type SnackbarCloseReason } from '@mui/material';
import { Action, type IAction } from '@strife/common';
import { injectable } from 'inversify';
import { type SyntheticEvent } from 'react';
import di from '../di';
import { ToastServiceServiceId, type IToastData, type IToastHandle, type IToastService } from './toast.service.interface';

@injectable()
class ToastService implements IToastService {

  private _key: string = crypto.randomUUID()
  public get key(): string {
    return this._key;
  }

  private _active?: IToastData;
  public get active(): IToastData | undefined {
    return this._active;
  }

  private _onChange: Action<[]> = new Action<[]>();
  public get onChange(): IAction<[]> {
    return this._onChange;
  }

  private _queue = new Array<IToastData>();
  private _toastDataByToastHandle = new Map<IToastHandle, IToastData>();
  
  public add(toastData: IToastData): IToastHandle {
    const toastHandle = new ToastHandle(this);
    toastData = this.configure(toastData);
    this._queue.push(toastData);
    this._toastDataByToastHandle.set(toastHandle, toastData);
    this.update();
    return toastHandle;
  }

  public remove(toastHandle: IToastHandle): void {
    const toastData = this._toastDataByToastHandle.get(toastHandle);
    if (!toastData)
      return;

    this._toastDataByToastHandle.delete(toastHandle);

    if (this._active == toastData) {
      this._active.open = false;
      this._active = undefined;
      this._key = crypto.randomUUID()
      this._onChange.invoke(this);
      this.update();
    } else {
      const index = this._queue.indexOf(toastData);
      if (index < 0)
        return;

      this._queue.splice(index, 1);
    }
  }

  private configure(toastData: IToastData): IToastData {
    const onClose = toastData.onClose;
    toastData.open = false;
    toastData.onClose = 
      (event: Event | SyntheticEvent<any, Event>, reason: SnackbarCloseReason): void => {
        if (onClose)
          onClose(event, reason);

        if (this._active == toastData) {
          this._active.open = false;
          this._active = undefined;
          this._key = crypto.randomUUID()
          this._onChange.invoke(this);
          this.update();
        }
      };

    return toastData;
  }

  private update(): void {
    if (this._active)
      return;

    if (this._queue.length == 0)
      return;

    this._active = this._queue.shift();
    this._key = crypto.randomUUID()
    if (!this._active)
      return;
    
    this._active.open = true;
    this._onChange.invoke(this);
  }
  
}

class ToastHandle implements IToastHandle {
  
  private _toastService: IToastService;
  
  public constructor(toasts: IToastService) {
    this._toastService = toasts;
  }

  public dismiss(): void {
    this._toastService.remove(this);
  }

}

di.bind(ToastServiceServiceId).to(ToastService).inSingletonScope();